<template>
  <div class="subscriber">
    <div class="details">
      <div class="title">訂單明細</div>
      <div v-html="details.replace(/\r?\n/g, '<br />')"></div>
      <div class="totalprice">
        <div class="title">總計</div>
        <div class="price">{{ price }}</div>
      </div>
    </div>
    <form>
      <div v-for="(data, key) in subscriberInfo" :key="key">
        <label>{{ data.label }}</label>
        <input
          :type="data.type"
          :name="data.name"
          :placeholder="data.placeholder"
          :autocomplete="data.autocomplete"
          v-model="data.value"
          @change="(event) => changeInfo(event)"
          @input="(event) => changeInfo(event)"
          required
        />
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "SubscriberView",
  props: {
    details: String,
    price: Number,
    subscriber: Object,
    // {
    //   name: "",
    //   office: "",
    //   phone: "",
    //   email: "",
    // }
  },
  data() {
    return {
      subscriberInfo: {
        name: {
          label: "姓名",
          type: "text",
          placeholder: "請輸入您的姓名",
          autocomplete: "name",
          value: this.subscriber.name,
          name: "name",
        },
        office: {
          label: "寢室/處室",
          type: "text",
          placeholder: "請輸入您的寢室/處室",
          autocomplete: "office",
          value: this.subscriber.office,
          name: "office",
        },
        phone: {
          label: "手機",
          type: "tel",
          placeholder: "09XXXXXXXX",
          autocomplete: "tel",
          value: this.subscriber.phone,
          name: "phone",
        },
        email: {
          label: "電子信箱",
          type: "email",
          placeholder: "請輸入您的電子信箱",
          autocomplete: "email",
          value: this.subscriber.email,
          name: "email",
        },
      },
    };
  },
  emits: ["subscriber-close", "info-change", "submitOrder"],
  methods: {
    close() {
      this.$emit("subscriber-close");
    },
    changeInfo(event) {
      var key = event.target.name;
      var data = event.target.value;
      this.$emit("info-change", key, data);
    },
  },
};
</script>

